                        <el-button  icon="el-icon-info" slot="reference" @click="SetSandardCampaign"> 设定标准推广方案 </el-button>
                        
                      </el-popover>
                      
                    
                    
                </div>
                <div class="text item campains-list" v-loading="loading">
                    
                    <el-row :gutter="24" class="campains-table-header">
