<script>
    import {request} from "@/network/request.js";
    
    export default {
        name: 'auto',
        data() {
            return {
                CampaignsList:[],
                show:true,
                campaignChecked:[], //勾选中的计划
                rtrpt:{},//账户实时报表
                rtrpt2:{},//账户昨天报表
                loading:false,                
    
            }
        },
        components:{
        },
        
        methods:{
            handleChooseGoods()
            {
                this.$refs.DrawerChooseGoodsList.drawer = true;
            },
            CreateCampaign()
            {
                
                request({
                    url: "/auto/CampaignAdd",
                    params:{
                        title:this.form.title,
                    },
                    method: 'get',
                }).then(res => {
                    console.log(res)
                    //this.data = res.data.data.items.item;
                    
                    this.$message({
                              message: res.data.data.msg + res.data.data.sub_msg,
                              type: 'success'
                            });
                })
                
            },
            //新建推广计划
            newCampaign()
            {
                this.$router.push({path:'/auto/viewCampaigns'})
            },
            viewCampaign(campaign)
            {
                this.$router.push({path:'/auto/viewCampaigns',query:campaign})
            },
            //taobao.simba.rpt.custeffect.get 用户账户报表效果数据查询
            RtrptCustGet()
            {
                
                request({
                    url: "/auto/RtrptCustGet",
                    params:{
                        "the_date":"2020-06-05"
                    }
                }).then(res => {
                    console.log(res)
                    this.rtrpt = res.data[0];
                    this.rtrpt2 = res.data[1];
                })
                
            },
            
            //设置自动优化
            SetAutoCampaign()
            {
                this.$confirm('是否继续批量设置自动优化?', '提示', {
                          confirmButtonText: '确定',
                          cancelButtonText: '取消',
                          type: 'warning'
                        }).then(() => {
                            
                            request({
                                url: "/auto/SetAutoCampaign",
                                params:{
                                    campaignChecked:this.campaignChecked,
                                }
                            }).then(res => {
                                console.log(res)
                                
                                this.$message({
                                  type: 'success',
                                  message: '操作成功!'
                                });
                        })

                })

            },
            
            //设置标准推广方案
            SetSandardCampaign()
            {
                this.$confirm('是否继续批量设置标准推广方案?', '提示', {
                          confirmButtonText: '确定',
                          cancelButtonText: '取消',
                          type: 'warning'
                        }).then(() => {
                            
                            
                            this.campaignChecked.forEach((item)=>{
                                
                                request({
                                    url: "/auto/InitCampaign()",
                                    params:{
                                        campaign_id:item,
                                    }
                                }).then(res => {
                                    console.log(res)
                                    
                                    this.$message({
                                      type: 'success',
                                      message: '操作成功!'
                                    });
                            })
                            
                            
                        })
            
                })
            
            }
            
        },
        mounted()
        {
            this.RtrptCustGet()
            this.loading=true;
            
            request({
                url: "/auto/GetCampaigns",
                method: 'get',
            }).then(res => {
                console.log("mount:", res)
                this.CampaignsList = res.data;
                this.loading=false;
                
            })

        }
        
    
    
    }
</script>

